.containerTestimonios{
    padding-bottom: 60px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    align-items: center;
    flex-direction: column;
}

.testimoniosTitle{
    font-size: 24px;
    padding: 20px;
    color: white;
    align-items: center;
    justify-items: center;
    text-align: center;
}

.testimoniosDescription{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 40px;
}

.bottomPanel {
    text-align: start;
    color: white;
    display: flex;
    align-items: center;
    align-self: flex-start;
    min-width: 276px;
    max-width: 400px;
    box-sizing: border-box;
    cursor: none;
    margin: 0 10px;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 20px;
    align-self: center;
    justify-self: center;
}


.logoJapon{
    width: 80px;
    height: 35px;
}

.panelContent {
    display: flex;
    flex-direction: column;
    /* Smooth transition without viewport interference */
    opacity: 0;
    transform: translateY(10px);
    animation: fadeInUp 0.3s ease-out forwards;
}

/* Smooth fade-in animation that doesn't affect scrolling */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.panelImage {
  object-fit: cover;
  border-radius: 20px;
}

.panelText { 
    color: white;
    margin-top: -60px;
    border: 1px solid #fef0ad;
    border-radius: 10px;
    background-color: rgb(17,17,17);
    text-align: center;
}

.panelName { 
    margin: 10px; 
    font-size: 14px;
    font-family: nohemi;
}

.panelQuote { 
    margin: 0; 
    line-height: 1.2;
    font-family: nohemi;
    font-size: 14px;
    padding: 10px;
}

.testimonioEstrellas { 
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10px;
    gap: 6px;
}

.starIcon { 
    width: 26px; 
    height: 26px; 
}
@media(max-width: 330px){
    .panelText{
    grid-area: 7 / 1 / 12 / 11;
    }

    .panelImage {
        height: 280px;
    }
}


@media (max-width: 720px) {
    .panelImage {
        height: 330px;
    }

    
}

.selected {
  outline: 2px solid #ffd166;
  border-radius: 10px;
}


.columnas{
    display: grid;
    grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .columnas {
    grid-template-columns: 1fr 1fr; /* 2 columns */
  }
}

@media (min-width: 768px) {
  .testimoniosDescription {
    display: flex;
    flex-direction: column;

  }
}